/* global Office */

import { parse } from "node-html-parser";
import { ClassificationType } from "../types/ClassificationType";
import { addSignature, getBody } from "../utils/addBody";
import { getClassification } from "../utils/getClassification";
import { getAttachments } from "../utils/readAttachments";
type AttachmentAndClassification = Office.AttachmentDetailsCompose & { classification: ClassificationType };
const attachmentsStyles = (attachment: AttachmentAndClassification) => {
  let arr: { color: string; name: string } = null;
  switch (attachment.classification.classificationLabel) {
    case "public":
      arr = {
        color: "#107c10",
        name: "Pblico",
      };
      break;
    case "restricted":
      arr = {
        color: "#d83b01",
        name: "Restringido",
      };
      break;
    case "confidential":
      arr = {
        color: "#e81123",
        name: "Confidencial",
      };
      break;
    case "internalUse":
      arr = {
        color: "#d29200",
        name: "Uso interno",
      };
      break;
    default:
      null;
  }
  return arr;
};
const attachmentsCache = [];
const onMessageAttachmentsChangedHandler = async (event) => {
  const attachmentsList = (await getAttachments()) || [];
  attachmentsCache.push(
    attachmentsList.filter(
      (attachment) => !attachmentsCache.includes(attachment.id) && !attachmentsCache.includes(attachment.name)
    )
  );
  const attachments = await Promise.all(
    attachmentsList.map(async (attachment) => ({
      ...attachment,
      classification: await getClassification(attachment),
    }))
  );
  const messageBody = await getBody();
  const tagedFilesComponent = (oldSignature: string) =>
    `
  <b>Kriptos Mailtag</b>
  <ul style='margin-top:0.1em;'>
  ${attachments.map((attachment) => {
    const style = attachmentsStyles(attachment);
    return `<li><div style='display:flex; align-items:center;'>${attachment.name + "&nbsp"}<b style="color:${
      style.color
    };">(${style.name})</b></div></li>`;
  })}
  </ul>
  <div id="close"></div>`.replace(/,/g, "") + `${oldSignature}`;
  const oldSignature = parse(messageBody.value)?.getElementById("Signature")?.innerHTML || "";
  const newSignature = oldSignature
    .replace(/\n/g, "")
    .replace(/<b>Kriptos Mailtag<\/b>(.*)<div id="x_close"><\/div>/, "")
    .replace(/\n/g, "");
  if (attachments.length === 0) {
    addSignature(newSignature);
  } else {
    addSignature(tagedFilesComponent(newSignature));
  }
  event.completed({ allowEvent: true });
};
Office.actions.associate("onMessageAttachmentsChangedHandler", onMessageAttachmentsChangedHandler);
